
/* 
=====================================================================
image-text__zigzag
=====================================================================
*/
.image-text-grey {
    display: flex;
    justify-content: space-between;
    background-color: @primary-grey-bg;
    &__content {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        &__wrapper {
            max-width: 75%;
            display: flex;
            gap: 10px;
            &--heading {
                display: flex;
                gap: 15px;
            }
            &--summary {
                font-size:16px;
                ul {
                    padding: 15px 0;
                    li {
                        margin-bottom: 15px;
                        display: flex;
                        svg {
                            margin-right: 10px;
                        }
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
                p {
                    margin: 16px 0;
                    font-size: 16px;
                }
            }
            &--summary-cta {
                .arrow-btn {
                    margin: 10px 0 0 0;
                }
            }
            &--summary-text {}
            &--svg {
                padding-top: 5px;
            }
        }
    }
    &__picture {
        width: 50%;
        position: relative;
        &--svg {
            svg {
                position: absolute;
                z-index: 3;
                top: 0;
                left: 0;
            }
        }
        &--image {
            width: 100%;
            height: 400px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    &.switch {
        .image-text-grey__content {
            order: 1;
        }
        .image-text-grey__picture {
            order: 2
        }
    }
}
@media (max-width: 768px) {
    .image-text-grey__content__wrapper {
        max-width: 100%;
        padding: 0 15px;
    }
}
@media (max-width: 600px) {
    .image-text-grey {
        flex-direction: column;
        &__content {
            width: 100%;
            &__wrapper {
                max-width: 100%;
                padding:40px 15px;
            }
        }
        &__picture {
            width: 100%;
        }
        &.switch {
            .image-text-grey__content {
                order: 2;
            }
            .image-text-grey__picture {
                order: 1;
            }
        }
    }
}